import React,{useState,useContext} from 'react';
import { useHistory } from 'react-router-dom';
import { CredentialsContext } from './App';
const handleErrors = async (response) => {
  if(!response.ok){
    const {message} = await response.json();
    throw Error(message);
  }
  return response.json();
}
function Login(){
  const history = useHistory()
  const [username, setusername]=useState("");
  const [userid, setuserid]=useState("");
  const [password, setpassword]=useState("");
  const [,setcredentials] = useContext(CredentialsContext);
  const [error,setError]=useState("");
  const login=(e)=>{
    e.preventDefault();
    fetch(`https://servervishnu.herokuapp.com/login`,{
      method: 'POST',
      headers: {"Content-Type":"application/json",
    },
    body: JSON.stringify({
      username,
      userid,
      password,
    }),
  })
  .then(handleErrors)
  .then(()=>{
    setcredentials({username,userid,password})
    history.push("/inbox");
  })
  .catch((error)=>{
    console.log(error);
    setError(error.message);
  })
};
  return(<div className="registercontainerp">
    <div className="registercontainer">
    <br />
    <br />
    <br />
    <h1>Login</h1>
    {error}
    <form onSubmit={login}>
      <label style={{fontSize:"20px",fontWeight:"bold"}}>Enter Full Name:</label>
      <input className="registeritems" onChange={(e)=>setusername(e.target.value)} placeholder="Enter Full Name" />
      <br />
      <label style={{fontSize:"20px",fontWeight:"bold"}}>Enter Email id:</label>
      <input className="registeritems" onChange={(e)=>setuserid(e.target.value)} placeholder="Enter Email id" />
      <br />
      <label style={{fontSize:"20px",fontWeight:"bold"}}>Enter Password:</label>
      <input className="registeritems" type="password" onChange={(e)=>setpassword(e.target.value)} placeholder="Password" />
      <br />
      <button className="registeritems" style={{width:"300px",fontSize:"25px",fontWeight:"bold",color:"white",borderRadius:"25px",cursor:"pointer",backgroundColor:"#AA2EE6"}} type="submit">Login</button>
    </form>
    </div>
  </div>)
}

export default Login;
