import React,{useState,useContext} from 'react';
// import styled from 'styled-components';
// import TextField from '@material-ui/core/TextField';
import './Register.css';
// import Button from '@material-ui/core/Button';
import { useHistory } from 'react-router-dom';
import { CredentialsContext } from './App';
const handleErrors = async (response) => {
  if(!response.ok){
    const {message} = await response.json();
    throw Error(message);
  }
  return response.json();
}
function Register(){
  const [,setcredentials] = useContext(CredentialsContext);
  const [username, setusername]=useState("");
  const [userid, setuserid]=useState("");
  const [password, setpassword]=useState("");
  const [error, seterror]=useState("");
  const [phone, setphone]=useState("9390182229");
  let history = useHistory();


  const register=(e)=>{
    e.preventDefault();
    fetch(`https://servervishnu.herokuapp.com/register`,{
      method: 'POST',
      headers: {"Content-Type":"application/json",
    },
    body: JSON.stringify({
      username,
      userid,
      password,
      phone,
    }),
  })
  .then(handleErrors)
  .then(()=>{
    setcredentials({username,userid,password})
    history.push("/inbox");
  })
  .catch((error)=>{
    console.log(error);
    seterror(error.message);
  })
};
  return(<div className="registercontainerp">
    <div className="registercontainer">
    <br />
    <br />
    <h1>Register</h1>
    {error}
    <form onSubmit={register}>
      <label style={{fontSize:"20px",fontWeight:"bold"}} className="registeritems">Enter Full Name:</label>
      <input className="registeritems" onChange={(e)=>setusername(e.target.value)} placeholder="Enter Full Name" />
      <br />
      <label style={{fontSize:"20px",fontWeight:"bold"}} className="registeritems">Enter Email id:</label>
      <input className="registeritems" onChange={(e)=>setuserid(e.target.value)} placeholder="Enter Email id" />
      <br />
      <label style={{fontSize:"20px",fontWeight:"bold"}} className="registeritems">Enter password:</label>
      <input className="registeritems" type="password" onChange={(e)=>setpassword(e.target.value)} placeholder="Password" />
      <br />
      <label style={{fontSize:"20px",fontWeight:"bold"}} className="registeritems">Enter Phone Number(optional):</label>
      <input className="registeritems" style={{width:"200px"}} type="tel" onChange={(e)=>setphone(e.target.value)} placeholder="Enter Phone Number(optional)" />
      <br />
      <br />
      <button style={{width:"300px",fontSize:"25px",fontWeight:"bold",color:"white",borderRadius:"25px",cursor:"pointer",backgroundColor:"#AA2EE6"}} type="submit">Register</button>
    </form>
    </div>
    </div>
    )
}

export default Register;
